var MODIFIER_TO_VALUE_DICT = {
    // delay
    d1: "delay-1s",
    d2: "delay-2s",
    d3: "delay-3s",
    d4: "delay-4s",
    d5: "delay-5s",
    // speed
    slow: "slow",
    slower: "slower",
    fast: "fast",
    faster: "faster",
    // times
    infinite: "infinite"
};
export function getModifierValue(key) {
    return MODIFIER_TO_VALUE_DICT[key];
}
